.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin 1s ease-in-out 0.5s alternate, move 5s linear 2s;
          animation: App-logo-spin 1s ease-in-out 0.5s alternate, move 5s linear 2s;;
  height: 30px;
}

.App-header {
}

.App-title {
  font-size: 1.5em;
}

.App-intro {
  font-size: large;
}

@-webkit-keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}



